*{
  margin: 0;
  padding:0;
}

.App{
  display: grid;
  justify-content: space-between;
  grid-template-columns: 70vw 30vw;
}

.mapcontainer{
  width:100%;
  height:100vh;
  display: flex;
  padding-top:20px;
  justify-content: center;
  align-items: flex-start;
}

.App{
    background-image:url('http://star--map.herokuapp.com/background/background.jpg');
    background-size:cover;
    background-repeat:no-repeat; 
}

.starmap_box{
  display: flex;
  flex-direction: column;
  margin:0;
  padding:0;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index:10;
  border:10px solid black;
}


.starmap_box img{
  width:400px;
  height:600px;
}
.starmap_box_circle{
  position: absolute;
  top: 10%;
}

.starmap_box_circle canvas{
  position: relative;
  z-index:-1;
  background-color: black;
  width:350px;
  height:350px;
}

.starmap_box h1{
  position: absolute;
  top:5%;
  font-size:18px;
  text-align: center;
}


.starmap_box p{
  text-align: center;
  position: absolute;
  bottom: 10%;
  font-size:18px;
  color: #000;
}

@media  screen and (max-width:600px) {
  .App{
    display: grid;
    justify-content: space-between;
    grid-template-columns:100vw;
  }

 
  .starmap_box_circle canvas{
    width:250px;
    height:250px;
  }
  .mapcontainer{
    height:auto;
    padding:2rem 0rem;
  }
}





.customecontainer{
  background-color: rgba(1, 2, 2, 0.5);
}


.customeheader{
  display: flex;
  justify-content: space-between;
  border-bottom:1px solid black ;
}
.customeheader li{
  list-style: none;
  text-align: center;
  width:100%;
  padding:1rem 0rem;
  font-size:25px;
  color: #000;
  cursor: pointer;
  
}
.customeheader li:hover{
  background-color: rgb(83, 79, 74);
}
.custome_body{
  display: flex;
  flex-wrap: wrap;
}
.custome_body img{
  width:100px;
  margin:1rem;
  height:100px;
  cursor: pointer;
}

.infobody{
  display: flex;
  width:100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.infobody input{
  padding:1rem 1rem;
  margin:1rem;
}
.infobody input:focus{
  outline: none;
}

.infobody label{
  color: #000;
  font-size: 1.5rem;
}
.infobody button{
  border: none;
  background: #8F00FF;
  padding:1rem 2rem;
  color: #fff;
  cursor: pointer;
}

.infolocationbody p{
  padding-top: 2rem;
  color:#ff0000;
  word-break: break-all;
}


.customise{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  background: #ffffff;
}
.customise{
  /* padding: 7px 10px 7px 10px; */
}

.btncontainer{
  display: inline-flex;
  justify-content: center;
  margin-top:20px;
  width: 100%;
}

.next-button{
  background: #8F00FF;
  position: relative;
  box-shadow: 0px 2px 8px rgba(102, 0, 183, 0.3);
  border-radius: 3px;
  height: 40px;
    width: 361px;
    top: auto;
    left: auto;
    margin: auto;
    color: #FFFFFF;
    border-style: none;
    font-size: 18px;
}





.starmapping-frame{
  background: url('http://localhost:3000/background/backimage.png');
  background-size: cover;
width: 50%;
background-repeat: no-repeat;
background-size: 100% 100%;
}


.starmapping-rects{
  background: #ffffff;
  width: 50%;
}

.frame-buttons{
  display:flex;
  position: absolute;
  top:25%;

  right:45%;
  height:50vh;
  flex-direction: column;
  justify-content: space-evenly;
}

.frame-link-buttons{
  /* position: absolute; */
  width: 128px;
  height: 38px;
  background: #DEDEDE;
  border-radius: 3px;
  border-style: none;
  color: #8C8C8C;
}

@media screen and (max-width:1150px){

  .frame-buttons{
    right:40%;
  }
  .select-your-frames{
    padding: 100px 10px 0px 10px;
  }
  .frame-buttons{
    top:5%;
    flex-wrap: wrap;
    height:100px;
    right: auto;
    flex-direction: row;
  }
  
}
.select-single-frame{
  height: 100px;
  width: 100px;
  background-color:#000;
  border:2px solid black;
  margin:2px;
  cursor: pointer;
}
.ImageContainer{
  display: grid;
    grid-template-columns:150px 150px;
    align-items: center;
    justify-content: center;
}


